<template>
  <div>
    <div class="value_box " style="margin-bottom: 20px;">
        <div class="value_box">
        <span>公司名称：</span>
         <el-input class="w200" v-model="OptionData.companyName"  placeholder="请输入内容"></el-input>
      </div>
      <div class="value_box">
        <span>岗位名称：</span>
         <el-select v-model="OptionData.positionName" class="w200" placeholder="请选择">
    <el-option
      v-for="item in AddArr"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
      </div>
       <div class="value_box">
        <span>要求学历：</span> <el-input class="w200" v-model="OptionData.education"  placeholder="请输入内容"></el-input>
      </div>
       <el-button type="primary" @click="getData">查询</el-button>
    </div>
  <el-table
    :data="tableData"
    style="width: 100%"
    >
        <el-table-column
      prop="companyName"
      label="公司名称"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="positionName"
      label="岗位名称"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="contractDuration"
      label="合同时长"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="education"
      label="要求学历"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="monthlySalary"
      label="月薪"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="positionNum"
      label="招聘人数"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="shipType"
      label="船舶类型"  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="updateTime"
      label="发布时间"  align="center"
      >
    </el-table-column>

     <el-table-column label="操作" width="100" align="center">
      <template slot-scope="item">

        <el-button type="primary"
          size="mini" @click="AddClickData(item.row.id)"
          >查看</el-button>

      </template>
    </el-table-column>
  </el-table>
  <div class="fy">
    <el-pagination
  background @current-change="pageClick"
  layout="prev, pager, next"
  :total="total">
</el-pagination>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      jobStatus: [
        { label: '未启用', value: '0' },
        { label: '已启用', value: '1' }
      ],
      total: 0,
      tableData: [

      ],
      OptionData: {
        pageNum: 1,
        pageSize: 10,
        statusFlag: 1
      }
    }
  },
  created () {
    this.getData()
  },
  methods: {
    open (value) {
      this.$confirm('此操作将永久删除该岗位, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios.post('/seaman-recruit/position/delete', [value]).then(res => {
          if (res.data.code === 200) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.getData()
          } else {
            this.$message.error(res.data.data)
          }
        })
      }).catch(() => {
      })
    },
    AddClickData (id) {
      this.$router.push({ path: '/home/user/JobList/JobDetails', query: { id } })
    },
    operate (id, value) {
      this.$axios.post('/seaman-recruit/position/updateById', { statusFlag: value, companyId: id, id: JSON.parse(localStorage.getItem('user')).id }).then(res => {
        if (res.data.code === 200) {
          this.$message({
            type: 'success',
            message: '操作成功!'
          })
          this.getData()
          // this.$router.back()
        } else {
          this.$message.error(res.data.data)
        }
      })
    },
    pageClick (value) {
      this.OptionData.pageNum = value
      this.getData()
    },
    getData () {
      this.$axios.post('/seaman-recruit/position/queryByPage', this.OptionData).then(res => {
        if (res.data.code === 200) {
          this.tableData = res.data.data.list
          this.total = res.data.data.total
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    AddClick () {
      this.$router.push('/home/JobListings/Add')
    },
    formatter (row, column) {
      return row.address
    }
  }
}
</script>

<style lang="scss" scoped>
.value_box{
  display: flex;
  align-items: baseline;
}

::v-deep.el-table th.el-table__cell{
  background: #F8F8F9;
}
.w200{
  width: 140px;
  margin: 0 10px;
}
</style>
