<template>
  <div>
    <h1 style="margin-bottom: 30px;">简历详情</h1>
  <el-form ref="form" :model="form" label-width="150px" v-if="form">
      <el-form-item :label="item.name+'：'" class="from_item" v-for="item,index in fromArr" :key="index">
         <!-- <el-date-picker v-if="item.type==='date'"
        v-model="form[item.key]"
      type="date" style="    width: 100%;"
      placeholder="选择日期">
    </el-date-picker>
          <el-input  v-if="item.type==='number'" v-model="form[item.key]" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
           <el-select v-model="form[item.key]" placeholder="请选择" v-if="item.type==='select'" class="w100">
            <el-option
              v-for="item in item.Arr"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select> -->
          <div v-if="item.type==='select'&&form[item.key]">{{item.Arr[form[item.key]].label}}</div>
          <div  v-if="item.type===undefined"  >{{form[item.key]}}</div>
  </el-form-item>

  </el-form>
  <div style="    text-align: center;" class="mtop">
  <el-button class="btn_l" size="small" @click="BtnBack">取消</el-button>
   <el-button class="btn_l" size="small" type="danger" v-if="$route.query.positionId">拒绝</el-button>
  <el-button type="primary" size="small" @click="interview">邀请面试</el-button>
  </div>
  <el-dialog
  title="选择岗位"
  :visible.sync="dialogVisible"
  width="30%"
  >
   <el-form ref="form" :model="positionId" label-width="100px" >
      <el-form-item label="岗位名称：" class="from_item"  style="width: 100%;">
           <el-select v-model="positionId" class="w100" placeholder="请选择">
            <el-option
              v-for="item in list"
              :key="item.id"
              :label="item.positionName"
              :value="item.id">
            </el-option>
          </el-select>
      </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>

    <el-button type="primary" @click="positionInterview">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>

export default {
  data () {
    return {
      positionId: null,
      dialogVisible: false,
      fromArr: [
        { key: 'appellation', name: '称谓' },
        { key: 'age', name: '年龄' },
        { key: 'sex', name: '性别' },
        { key: 'height', name: '身高' },
        { key: 'nation', name: '民族' },
        { key: 'nativePlace', name: '籍贯' },
        { key: 'boardingPlace', name: '学历' },
        { key: 'graduationSchool', name: '毕业学校' },
        { key: 'graduationTime', name: '毕业时间' },
        { key: 'certificateLevel', name: '证书等级' },
        { key: 'education', name: '现居住地' },
        { key: 'currentResidence', name: '上船地点' },
        { key: 'experienceSailing', name: '航海经验' },
        { key: 'jobApplication', name: '应聘职位' },
        { key: 'major', name: '专业' },
        { key: 'maritalStatus', name: '婚姻状况' },
        { key: 'monthlySalary', name: '要求月薪' },
        { key: 'originalPosition', name: '原任职务' },
        { key: 'politicalOutlook', name: '政治面貌' },
        { key: 'requiredGross', name: '要求总吨' },
        { key: 'routeRequirements', name: '航线要求' },
        { key: 'shipRequirements', name: '船舶要求' },
        { key: 'technicalSkill', name: '特殊技能' },
        {
          key: 'userStatus',
          name: '在职状态',
          type: 'select',
          Arr: [
            { value: '0', label: '在职不考虑机会' },
            { value: '1', label: '在职考虑机会' },
            { value: '2', label: '离职状态' }
          ]
        }
      ],
      form: {

      },
      list: [],
      url: '/seaman-recruit/resume/add'

    }
  },
  methods: {
    positionInterview () {
      const datas = {
        companyUserId: JSON.parse(localStorage.getItem('user')).id,
        positionId: this.positionId,
        userId: this.$route.query.id
      }
      this.$axios.post('/seaman-recruit/position/interview', datas).then(res => {
        if (res.data.code === 200) {
          this.$message({
            type: 'success',
            message: '发送成功!'
          })
          this.$router.go(-1)
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    interview () {
      if (this.$route.query.positionId) {
        const datas = {
          companyUserId: JSON.parse(localStorage.getItem('user')).id,
          positionId: this.$route.query.positionId,
          userId: this.$route.query.id
        }
        this.$axios.post('/seaman-recruit/position/interview', datas).then(res => {
          if (res.data.code === 200) {
            this.$message({
              type: 'success',
              message: '发送成功!'
            })
            this.$router.go(-1)
          } else {
            console.log(res.data.data)
            this.$message.error(res.data.data)
          }
        })
      } else {
        this.$axios.post('/seaman-recruit/position/queryByPage', { pageNum: 1, pageSize: 1000 }).then(res => {
          if (res.data.code === 200) {
            this.list = res.data.data.list
            this.dialogVisible = true
          } else {
            console.log(res.data.data)
            this.$message.error(res.data.data)
          }
        })
      }
    },
    getData () {
      this.$axios.get('/seaman-recruit/resume/', this.$route.query.id).then(res => {
        if (res.data.code === 200) {
          if (res.data.data && res.data.data.appellation) {
            this.form = res.data.data
            this.url = '/seaman-recruit/resume/updateById' // 判断修改还是新增
          }
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    BtnBack () {
      this.$router.back()
    },
    BtnClick () {

    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
::v-deep.from_item .el-form-item__label{
  color: #000;
  font-size:16px;
}
::v-deep.el-form-item{
  margin-bottom:5px
}
.btn_l{
  margin-right: 40px;
}
.btn_r{
  margin-left: 40px;
}
.mtop{
  margin-top:20px
}
.el-form-item{
  width: 50%;
  display:inline-block;
}
</style>
