<template>
  <div>
    <h1 style="margin-bottom: 30px;">岗位详情</h1>
  <el-form ref="form" :model="form" label-width="150px" v-if="form">
      <el-form-item :label="item.name+''" class="from_item" v-for="item,index in fromArr" :key="index">
          <div v-if="item.type==='select'">{{item.Arr[form[item.key]].label}}</div>
          <div  v-if="item.type===undefined"  >{{form[item.key]}}</div>
  </el-form-item>

  </el-form>
  <div style="    text-align: center;" class="mtop">
  <el-button class="btn_l" size="small" @click="BtnBack">取消</el-button>
  <el-button type="primary" size="small" :disabled="form.deliveryFlag==1" class="btn_r" @click="BtnClick">{{form.deliveryFlag==1?'已投递':'简历投递'}}</el-button>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      fromArr: [
        { key: 'companyName', name: '公司名称：' },
        { key: 'companyType', name: '公司性质：' },
        { key: 'capital', name: '注册资金：' },
        { key: 'year', name: '成立年份：' },
        { key: 'positionName', name: '职位名称：' },
        { key: 'education', name: '学历要求：' },
        { key: 'monthlySalary', name: '月薪：' },
        { key: 'shipAge', name: '船龄：' },
        { key: 'shipType', name: '船舶类型：' },
        { key: 'specificRoute', name: '具体航线：' },
        { key: 'contractDuration', name: '合同时长(年)：' },
        { key: 'boardingPlace', name: '上船地点：' },
        { key: 'positionNum', name: '招聘人数：' },
        { key: 'grossTon', name: '总吨/马力：' }

      ],
      form: {

      },
      url: '/seaman-recruit/resume/add'

    }
  },
  methods: {
    getData () {
      this.$axios.get('seaman-recruit/position/queryById', '?id=' + this.$route.query.id).then(res => {
        if (res.data.code === 200) {
          console.log(res.data.data)
          this.form = res.data.data
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    BtnBack () {
      this.$router.back()
    },
    BtnClick () {
      this.$axios.get('/seaman-recruit/position/delivery', '?id=' + this.$route.query.id).then(res => {
        if (res.data.code === 200) {
          this.$message({
            message: '简历投递成功！',
            type: 'success'
          })
          this.$router.go(-1)
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
::v-deep.from_item .el-form-item__label{
  color: #000;
  font-size:16px;
}
::v-deep.el-form-item{
  margin-bottom:5px
}
.btn_l{
  margin-right: 40px;
}
.btn_r{
  margin-left: 40px;
}
.mtop{
  margin-top:20px
}
.el-form-item{
  width: 50%;
  display:inline-block;
}
</style>
